<template>
  <div class="ShopPageContainer">
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1"><i class="el-icon-s-home"></i></el-menu-item>
        <el-menu-item index="2"><i class="el-icon-user-solid"></i></el-menu-item>
        <el-menu-item index="6" class="right-menu-2">Log Out</el-menu-item>
      </el-menu>
      <div class='player'>
        <video-player class="video-player vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions">
        </video-player>
      </div>
  </div>
</template>
<script>
//import { videoPlayer } from 'vue-video-player'

export default {
    name: 'PlayPage',
/*
    components: {
        videoPlayer
    }
    */
  data() {
      return {
        activeIndex: '1',
        playerOptions: {
              playbackRates: [0.5, 1.0, 1.5, 2.0], // 
              autoplay: false, // true,
              muted: false, // 
              loop: false, // 
              preload: 'auto', // <video>auto,
              aspectRatio: '16:9', //  - "16:9""4:3"
              fluid: true, // trueVideo.js player
              sources: [{
                  type: "video/mp4", // 
                  src: require('../assets/presenter.mp4') // url
              }],
              //poster: require('../assets/presentation.jpeg'), // 
              notSupportedMessage: '', // Video.js
              controlBar: {
                  timeDivider: true, // 
                  durationDisplay: true, // 
                  remainingTimeDisplay: false, // 
                  fullscreenToggle: true // 
              }
        }
    }
    },
    methods: {
        handleSelect(key) {
            if (key === '1') {
                this.onClickHome()
            } else if (key === '2') {
                this.onClickProfile('info','1')
            } else {
                this.onClickLogout()
            }
        },
        onClickHome() {
            this.$router.push('/');
        },
        onClickProfile() {
            this.$router.push('profile')
        },
        onClickLogout() {
            this.$store.dispatch('user/logout')
            .then(() => {
                this.$router.push('/')
            })
            .catch(() => {
                console.log("logout failure")
            })
        },
    }
} 
</script>
<style scoped>
    .player {
        height: 80%;
        width: 70%;
        margin: auto;
        padding-top: 5%;
    }
</style>